import React, { useState } from 'react'
import css from "../css/howtocreate.module.css"
import { Link } from 'react-router-dom'

const HowToCreate = () => {
    useState(() => {
        document.title = "MCWIKI | Hướng dẫn"
    }, [])

    return (
        <div className="container">
            <div className='tab'>
                <div>
                    <center><h2 style={{ fontWeight: "500" }}>BỐ CỤC ĐỂ TẠO RA MỘT BÀI VIẾT</h2></center>
                    <img alt="" className={css.howtocreateimg} src="/src/image/huongdan.png"/>
                    <p>Trường đánh dấu <span style={{color: "red"}}>*</span> là trường bắt buộc</p>
                    <p>Người dùng có thể định dạng kiểu các trường (Tiêu đề, mô tả,...), có thể là căn giữa, in nghiêng ở chỗ cần thiết,... Nhưng vẫn phải tuân thủ một số nguyên tắc sau:</p>
                    <div className='container'>
                        <p>1: Không sử dụng các từ ngữ tục tĩu, xúc phạm các vị anh hùng dân tộc,...</p>
                        <p>2: Chỉ nên căn chỉnh định dạng kiểu ở chỗ cần thiết, chỗ không cần thiết vui lòng không lạm dụng</p>
                        <p>3: Phần tiêu đề bắt buộc sử dụng định dạng kiểu LARGE</p>
                        <p>4: Vui lòng không chỉnh sửa file .txt khi tải về của wiki hoặc sử dụng DevTools trên trình duyệt để chỉnh sửa mã nguồn nhằm tạo ra đoạn mã độc hại</p>
                        <p>5: Trong command và lập trình, sử dụng [Tên: Kiểu dữ liệu] để biểu diễn giá trị có thể bỏ qua, sử dụng &lt;Tên: Kiểu dữ liệu&gt; để biểu diễn giá trị không thể bỏ qua, chi tiết về kiểu dữ liệu, xem tại <Link to="/">Trang chủ</Link></p>
                    </div>
                    <p>Sau khi đã tạo xong bài viết, vui lòng gửi file .txt thông qua facebook: <Link to="https://www.facebook.com/profile.php?id=100086572587409">https://www.facebook.com/profile.php?id=100086572587409</Link></p>
                    <p>+ Yêu cầu dành cho kiểm duyệt viên:</p>
                    <div className='container'>
                        <p>- Vui lòng sử dụng tab ẩn danh và không đăng nhập vào bất kì trang web nào để kiểm tra, giảm thiểu khả năng bị dính các mã độc, để an toàn hơn, các KDV có thể sử dụng máy ảo như: VMOS, VirtualBox, Vmware,...</p>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default HowToCreate