import React, { useState } from 'react'
import css from "../css/settings.module.css"
import cookies from "cookie-universal"

const Settings = () => {
    const Cookies = new cookies()

    const displayType = useState(Cookies.get("displaytype"))

    return (
        <div className={`container ${css.settings}`}>
            <h3>ĐANG TRONG QUÁ TRÌNH PHÁT TRIỂN, SẼ CÓ SAU</h3>
            <div>Chọn kiểu hiển thị của bạn: <select>
                <option>Hiển thị kiểu lá bài</option>
                <option defaultChecked={displayType === "ng" ? true : false}>Hiển thị kiểu nằm ngang</option>
            </select></div>
        </div>
    )
}

export default Settings